import React, { useState, useMemo } from "react"
import { TransformMatrix } from "../../helpers/Matrix"
import "../../styles/styles.css"
import { Path, Track, pathStrings } from "../../helpers/Paths"
import { Lines } from "./Lines"
import Draggable from "react-draggable"
import {
  Box,
  Checkbox,
  Divider,
  FormControl,
  FormControlLabel,
  FormGroup,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  Slider,
} from "@mui/material"
import { Show } from "../../helpers/types"

interface SourcePlotterProps {
  label: string
  image: string
  WIDTH: number
  HEIGHT: number
  filterLength: boolean
  minLength: number
  maxLength: number
  tracks: Track[]
  from: number
  to: number
  showType: string[]
}

const SourcePlotter: React.FC<SourcePlotterProps> = ({
  label,
  image,
  WIDTH,
  HEIGHT,
  tracks,
  from,
  to,
  showType,
  filterLength,
  minLength,
  maxLength,
}) => {
  const [scale, setScale] = useState<number>(1)
  const [filters, setFilters] = useState<Show>({
    markMeasurements: false,
    markEstimated: false,
    onlyConfirmed: false,
    trackId: false,
    showLines: false,
  })
  const [selectedIndices, setSelectedIndices] = useState<Map<number, boolean>>(
    new Map()
  )
  /**
   * Memoize the lines so we don't recalculate pathStrings everytime
   * we rerender this component
   */
  const paths = useMemo(() => {
    return pathStrings(
      tracks.slice(from, to),
      showType,
      filters,
      filterLength,
      minLength,
      maxLength
    )
  }, [from, to, showType, filters, filterLength, minLength, maxLength])

  const handleSliderChange = (event: Event, newValue: number | number[]) => {
    if (typeof newValue === "number") {
      setScale(newValue)
    }
  }

  const SVGLines = (
    <Lines
      WIDTH={WIDTH}
      HEIGHT={HEIGHT}
      scale={scale}
      pathStrings={paths}
      showId={filters.trackId}
      selectedIndices={selectedIndices}
      vertices={[]}
    />
  )

  function sortedIndex(sortedArray: number[], value: number) {
    let low = 0,
      high = sortedArray.length

    while (low < high) {
      const mid = (low + high) >>> 1
      if (sortedArray[mid] < value) low = mid + 1
      else high = mid
    }
    return low
  }

  const handleListItemClick = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
    index: number
  ) => {
    setSelectedIndices((indices) => new Map(indices.set(index, true)))
  }

  const renderRows = (rows: Path[]) => {
    return rows.map((path, index) => {
      return (
        <ListItemButton
          key={index}
          selected={selectedIndices.has(index)}
          onClick={(event) => {
            if (!selectedIndices.has(index)) handleListItemClick(event, index)
            else
              setSelectedIndices((indices) => {
                indices.delete(index)
                return new Map(indices)
              })
          }}
        >
          <ListItemText primary={path[2]} />
          <ListItemText primary={path[1]} />
        </ListItemButton>
      )
    })
  }

  return (
    <div>
      <div className="control-container">
        <Slider
          value={scale}
          aria-label="Default"
          valueLabelDisplay="auto"
          onChange={handleSliderChange}
          min={0}
          max={10}
          step={0.01}
          color="primary"
          className="slider"
          style={{ width: 300, margin: 0 }}
        />
      </div>
      <div style={{ overflow: "hidden", display: "flex" }}>
        <div style={{ display: "flex", flexDirection: "column" }}>
          <Draggable>
            <div
              className="image-container"
              style={{
                width: WIDTH * scale,
                height: HEIGHT * scale,
                backgroundImage: `url(${image})`,
              }}
            >
              {SVGLines}
            </div>
          </Draggable>
          <div style={{ marginTop: 10 }}>
            <List
              sx={{
                width: "100%",
                maxWidth: 360,
                bgcolor: "background.paper",
                position: "relative",
                overflow: "auto",
                maxHeight: 400,
                "& ul": { padding: 0 },
                borderRadius: 1,
              }}
              subheader={<ListSubheader>Tracks</ListSubheader>}
              component="nav"
              aria-label="main mailbox folders"
            >
              <Divider />
              {renderRows(paths)}
            </List>
          </div>
        </div>
        <FormControl
          sx={{ margin: 0, marginLeft: 2 }}
          component="fieldset"
          variant="standard"
        >
          <FormGroup>
            <FormControlLabel
              control={
                <Checkbox
                  checked={filters.markMeasurements}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setFilters((prevFilters) => ({
                      ...prevFilters,
                      markMeasurements: event.target.checked,
                    }))
                  }}
                />
              }
              label="Mark measurments"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={filters.markEstimated}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setFilters((prevFilters) => ({
                      ...prevFilters,
                      markEstimated: event.target.checked,
                    }))
                  }}
                />
              }
              label="Mark estimated"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={filters.onlyConfirmed}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setFilters((prevFilters) => ({
                      ...prevFilters,
                      onlyConfirmed: event.target.checked,
                    }))
                  }}
                />
              }
              label="Show only confirmed"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={filters.trackId}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setFilters((prevFilters) => ({
                      ...prevFilters,
                      trackId: event.target.checked,
                    }))
                  }}
                />
              }
              label="Show track id"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={filters.showLines}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setFilters((prevFilters) => ({
                      ...prevFilters,
                      showLines: event.target.checked,
                    }))
                  }}
                />
              }
              label="Show lines"
            />
          </FormGroup>
        </FormControl>
      </div>
    </div>
  )
}

export default SourcePlotter
