import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  OutlinedInput,
  MenuItem,
  Checkbox,
  ListItemText,
  FormControlLabel,
  Button,
  SelectChangeEvent,
} from "@mui/material"
import {
  LocalizationProvider,
  DatePicker,
  TimePicker,
} from "@mui/x-date-pickers"
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs"
import { names } from "../../../helpers/consts"
import dayjs, { Dayjs } from "dayjs"

interface HeaderProps {
  date: Dayjs
  setDate: (value: React.SetStateAction<dayjs.Dayjs>) => void
  fromTime: Dayjs
  setFromTime: (value: React.SetStateAction<dayjs.Dayjs>) => void
  setToTime: (value: React.SetStateAction<dayjs.Dayjs>) => void
  interval: number
  setDir: (value: React.SetStateAction<0 | 1 | -1>) => void
  setInterval: (value: React.SetStateAction<number>) => void
  showType: string[]
  handleChange: (event: SelectChangeEvent<string[]>) => void
  filterLength: boolean
  setFilterLength: (value: React.SetStateAction<boolean>) => void
  minLength: number
  setMinLength: (value: React.SetStateAction<number>) => void
  maxLength: number
  setMaxLength: (value: React.SetStateAction<number>) => void
  handleBackwardClick: () => void
  handleForwardClick: () => void
}

export const Header: React.FC<HeaderProps> = ({
  date,
  setDate,
  fromTime,
  setFromTime,
  setToTime,
  interval,
  setDir,
  setInterval,
  showType,
  handleChange,
  filterLength,
  setFilterLength,
  minLength,
  setMinLength,
  maxLength,
  setMaxLength,
  handleBackwardClick,
  handleForwardClick,
}) => {
  const ITEM_HEIGHT = 48
  const ITEM_PADDING_TOP = 8
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  }
  console.log(showType)
  return (
    <div>
      <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DatePicker
          label="Date"
          format="YYYY-MM-DD"
          value={date}
          onChange={(newValue) => {
            if (newValue) setDate(newValue)
          }}
          sx={{
            backgroundColor: "background.default",
            borderRadius: 1,
            marginRight: 2,
          }}
        />
        <TimePicker
          label="From time"
          value={fromTime}
          onChange={(newValue) => {
            if (newValue) {
              setFromTime(newValue)
              setToTime(newValue.add(interval, "minutes"))
              setDir(0)
            }
          }}
          ampm={false}
          sx={{
            backgroundColor: "background.default",
            borderRadius: 1,
            marginRight: 2,
          }}
        />
      </LocalizationProvider>
      <TextField
        label="Interval in minutes"
        id="outlined-start-adornment"
        sx={{
          width: "25ch",
          backgroundColor: "background.default",
          borderRadius: 1,
          margin: 0,
          marginRight: 2,
        }}
        value={interval}
        onChange={(newVal) => {
          const val = Number(newVal.target.value)
          if (!isNaN(val)) {
            setInterval(val)
            setToTime(fromTime.add(val, "minutes"))
          }
        }}
      />
      <FormControl sx={{ m: 1, width: 300, margin: 0 }}>
        <InputLabel id="demo-multiple-name-label">Show types</InputLabel>
        <Select
          labelId="demo-multiple-name-label"
          id="demo-multiple-name"
          multiple
          value={showType}
          onChange={handleChange}
          input={<OutlinedInput label="Show types" />}
          renderValue={(selected) => selected.join(", ")}
          MenuProps={MenuProps}
          sx={{ backgroundColor: "background.default" }}
        >
          {names.map((name) => (
            <MenuItem key={name} value={name}>
              <Checkbox checked={showType.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <div>
        <FormControlLabel
          control={
            <Checkbox
              checked={filterLength}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                setFilterLength(event.target.checked)
              }}
            />
          }
          label="Filter length"
          style={{ margin: 0, marginTop: 10 }}
        />
      </div>
      {filterLength && (
        <div style={{ marginTop: 10 }}>
          <TextField
            label="Minimum length"
            id="outlined-start-adornment"
            sx={{
              m: 1,
              width: "25ch",
              backgroundColor: "background.default",
              borderRadius: 1,
              margin: 0,
              marginRight: 2,
            }}
            value={minLength}
            onChange={(newVal) => {
              const val = Number(newVal.target.value)
              if (!isNaN(val)) {
                setMinLength(val)
              }
            }}
          />
          <TextField
            label="Maximum length"
            id="outlined-start-adornment"
            sx={{
              m: 1,
              width: "25ch",
              backgroundColor: "background.default",
              borderRadius: 1,
              margin: 0,
              marginRight: 2,
            }}
            value={maxLength}
            onChange={(newVal) => {
              const val = Number(newVal.target.value)
              if (!isNaN(val)) {
                setMaxLength(val)
              }
            }}
          />
        </div>
      )}
      <div style={{ margin: 0, marginTop: 10 }}>
        <Button
          sx={{ marginRight: 2, backgroundColor: "background.default" }}
          onClick={() => handleBackwardClick()}
        >
          {"<"}
        </Button>
        <Button
          sx={{ marginRight: 2, backgroundColor: "background.default" }}
          onClick={() => handleForwardClick()}
        >
          {">"}
        </Button>
      </div>
    </div>
  )
}
