import React from "react"
import { colors } from "../../helpers/consts"
import { Path } from "../../helpers/Paths"

type LinesProps = {
  WIDTH: number
  HEIGHT: number
  scale: number
  pathStrings: Path[]
  showId: boolean
  selectedIndices: Map<number, boolean>
  vertices: JSX.Element[]
}

export const Lines: React.FC<LinesProps> = ({
  WIDTH,
  HEIGHT,
  scale,
  pathStrings,
  showId,
  selectedIndices,
  vertices,
}) => {
  return (
    <svg
      width={WIDTH * scale}
      height={HEIGHT * scale}
      style={{
        transform: `scale(${scale})`,
        transformOrigin: "top left", // Set the transformation origin
      }}
    >
      {pathStrings.map((path, index) => {
        /**
         * The strings are of the format "...L {x-ending point} {y-ending point},
         * we want to get these coordinates to display the starting ids above
         * the ending point"
         */
        return !selectedIndices.has(index) ? (
          <g key={path[0]}>
            <path d={path[0]} stroke={colors[path[1]]} fill="none" />
            <circle
              cx={path[3][0]}
              cy={path[3][1]}
              r={4}
              fill="green"
              strokeWidth="4"
            />
            <circle
              cx={path[4][0]}
              cy={path[4][1]}
              r={4}
              fill="red"
              strokeWidth="4"
            />
            {showId && (
              <text
                x={path[4][0]}
                y={path[4][1]}
                fill="white"
                fontFamily="sans-serif"
                fontSize={10}
              >
                {path[2]}
              </text>
            )}
          </g>
        ) : (
          <></>
        )
      })}
      {vertices}
    </svg>
  )
}
