import React, { useEffect, useState } from "react"
import SourcePlotter from "./SourcePlotter"
import { useRenderData } from "streamlit-component-lib-react-hooks"
import { Track } from "../../helpers/Paths"
import dayjs, { Dayjs } from "dayjs"
import { SelectChangeEvent } from "@mui/material"
import { names } from "../../helpers/consts"
import { Streamlit } from "streamlit-component-lib"
import { Header } from "./header/Header"

const TrackAnalysis: React.FC = () => {
  const renderData = useRenderData()
  const [from, setFrom] = useState(0)
  const [to, setTo] = useState(0)
  const [dir, setDir] = useState<0 | 1 | -1>(0)
  const [date, setDate] = useState<Dayjs>(dayjs(renderData.args["date"]))
  const [fromTime, setFromTime] = useState<Dayjs>(
    dayjs(renderData.args["date"])
  )
  const [toTime, setToTime] = useState<Dayjs>(dayjs(renderData.args["date"]))
  const [interval, setInterval] = useState(1)
  const [showType, setShowType] = React.useState<string[]>(names)
  const [filterLength, setFilterLength] = useState(false)
  const [minLength, setMinLength] = useState(0)
  const [maxLength, setMaxLength] = useState(0)
  const [tracks, setTracks] = useState<Track[]>(renderData.args["tracks"])
  const ground = renderData.args["image"]
  const height = renderData.args["height"]
  const width = renderData.args["width"]

  useEffect(() => {
    if (date.isValid()) {
      Streamlit.setComponentValue(date.format("YYYY-MM-DD"))
    }
  }, [date])

  useEffect(() => {
    const setIndices = () => {
      let fromIndex: number
      let toIndex: number
      if (dir === 0) {
        fromIndex = binarySearch(fromTime, 0, tracks.length - 1)
        toIndex = binarySearch(toTime, 0, tracks.length - 1)
      } else if (dir === 1) {
        fromIndex = to + 1
        let i = fromIndex
        console.log("INDEX:", i, tracks.length - 1, i < tracks.length - 1)
        while (i < tracks.length && dayjs(tracks[i][5]).isBefore(toTime)) {
          i++
        }
        toIndex = i
      } else {
        toIndex = from
        let i = toIndex
        console.log("INDEX:", i, tracks.length)
        while (
          i > 0 &&
          i < tracks.length &&
          fromTime.isBefore(dayjs(tracks[i][5]))
        ) {
          i--
        }

        fromIndex = i + 1
      }
      setFrom(fromIndex)
      setTo(toIndex)
      console.log("First track:", tracks[fromIndex])
      console.log("Last track", tracks[toIndex - 1])
      console.log(`From ${fromIndex} to ${toIndex}`)
    }
    setIndices()
  }, [fromTime, toTime])

  const handleBackwardClick = () => {
    setFromTime((fromTime) => fromTime.subtract(interval, "minutes"))
    setToTime((toTime) => toTime.subtract(interval, "minutes"))
    setDir(-1)
  }

  const handleForwardClick = () => {
    setFromTime((fromTime) => fromTime.add(interval, "minutes"))
    setToTime((toTime) => toTime.add(interval, "minutes"))
    setDir(1)
  }

  function binarySearch(time: Dayjs, low: number, high: number): number {
    while (low < high) {
      const mid = Math.floor((low + high) >>> 1)
      if (mid === 0) return 0
      const midTime = dayjs(tracks[mid][5])
      if (midTime.isAfter(time) && dayjs(tracks[mid - 1][5]).isBefore(time)) {
        return mid
      } else if (midTime.isAfter(time)) {
        high = mid
      } else {
        low = mid + 1
      }
    }
    return low
  }

  const cameraPlots: JSX.Element[] = [
    <SourcePlotter
      image={ground}
      WIDTH={width}
      HEIGHT={height}
      tracks={tracks}
      from={from}
      to={to}
      label={"Version 1"}
      showType={showType}
      filterLength={filterLength}
      minLength={minLength}
      maxLength={maxLength}
    />,
  ]

  const handleChange = (event: SelectChangeEvent<typeof showType>) => {
    const {
      target: { value },
    } = event
    setShowType(typeof value === "string" ? value.split(",") : value)
  }

  return (
    <div style={{ flexDirection: "column", marginTop: 30 }}>
      <Header
        date={date}
        setDate={setDate}
        fromTime={fromTime}
        setFromTime={setFromTime}
        setToTime={setToTime}
        interval={interval}
        setDir={setDir}
        setInterval={setInterval}
        showType={showType}
        handleChange={handleChange}
        filterLength={filterLength}
        setFilterLength={setFilterLength}
        minLength={minLength}
        setMinLength={setMinLength}
        maxLength={maxLength}
        setMaxLength={setMaxLength}
        handleBackwardClick={handleBackwardClick}
        handleForwardClick={handleForwardClick}
      />
      <div style={{ display: "flex", flexDirection: "row" }}>
        <div
          style={{
            flexDirection: "row",
            flexWrap: "wrap",
          }}
        >
          {cameraPlots.map((plot) => plot)}
          <div style={{ height: 10000 }} />
        </div>
      </div>
    </div>
  )
}

export default TrackAnalysis
