import { Pos, TransformMatrix, transformPos } from "./Matrix"
import { names } from "./consts"
import { Show } from "./types"

export type Track = [number, number, number, number, number, string]
export type Path = [string, number, number, Pos, Pos]
type InternalTrack = [number[], number[], number, number, number[]]

export function pathStrings(
  entries: Track[],
  showType: string[],
  filters: Show,
  filterLength: boolean,
  minLength: number,
  maxLength: number
): Path[] {
  const trackMap = new Map<number, InternalTrack>()
  for (const [id, x, y, type, estimated] of entries) {
    if (
      (filters.onlyConfirmed && estimated === 1) ||
      showType.indexOf(names[type]) === -1
    ) {
    } else {
      if (trackMap.has(id)) {
        const [
          xCoordinates,
          yCoordinates,
          existingType,
          existingId,
          estimates,
        ] = trackMap.get(id)!
        xCoordinates.push(x * 20 + 280)
        yCoordinates.push(y * 20 + 300)
        estimates.push(estimated)
      } else {
        trackMap.set(id, [
          [x * 20 + 280],
          [y * 20 + 300],
          type,
          id,
          [estimated],
        ])
      }
    }
  }

  trackMap.forEach((track, key) => {
    if (
      filterLength &&
      (track[0].length < minLength || track[0].length > maxLength)
    ) {
      trackMap.delete(key)
    }
  })

  return Array.from(trackMap.values(), (track) =>
    createPath(track, filters.markMeasurements, filters.markEstimated)
  )
}

function createPath(
  track: InternalTrack,
  markMeasured: boolean,
  markEstimates: boolean
): Path {
  {
    const [xs, ys, type, id, estimates] = track
    const startPos: Pos = [xs[0], ys[0], 0]
    const endPos: Pos = [xs[xs.length - 1], ys[ys.length - 1], 0]
    const pathData = xs
      .map((x, i) => {
        const estimated = estimates[i]
        let pathSegment = `L ${x} ${ys[i]}`
        if (
          ((markMeasured && estimated === 0) ||
            (markEstimates && estimated === 1 && i !== 0)) &&
          i !== 0
        ) {
          const size = estimated === 0 ? 6 : 3
          const [x1, y1] = [xs[i - 1], ys[i - 1]]
          const [x2, y2] = [x, ys[i]]
          const length = Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2))
          const perpendicularDir = [(y1 - y2) / length, (x2 - x1) / length]
          const angle = 0.707
          const offsetX =
            perpendicularDir[0] * angle - perpendicularDir[1] * angle
          const offsetY =
            perpendicularDir[0] * angle + perpendicularDir[1] * angle
          pathSegment += `L ${x + offsetX * size} ${
            ys[i] + offsetY * size
          } L ${x} ${ys[i]} L ${x - offsetY * size} ${
            ys[i] + offsetX * size
          } L ${x} ${ys[i]}`
        }
        return pathSegment
      })
      .join(" ")
    const pathString = `M ${xs[0]} ${ys[0]} ${pathData}`
    return [pathString, type, id, startPos, endPos]
  }
}
